body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #ffff;

}

.sidebar {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #ffff;
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;
}

.sidebar a {
  padding: 15px 10px;
  text-decoration: none;
  font-size: 25px;
  color: #818181;
  display: block;
  transition: 0.3s;
}

.sidebar a:hover {
  color: #f1f1f1;
}

.sidebar .close-btn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 30px;
  margin-left: 50px;
}

.main {
  transition: margin-left 0.5s;
  padding: 16px;
}

.open-btn {
  font-size: 30px;
  cursor: pointer;
position: relative;
top: -3.5rem;
margin-left: -13px;
}

.sidebar.open {
  width: 250px;
}
.App2{
  box-shadow:  0px 0px 5px -2px black;
  height: 70px;
}

.main.open {
  margin-left: 250px;
}

@media screen and (min-width: 900px) {
  .App2{
   display: none;
  }

  
 }

